  width: 1720px;
  margin: 0 auto;
  padding-top: 100px;
}
.contact{
  font-size: 124px;
  font-weight: bold;
}

.contactTxt{
  font-size: 124px;
  font-weight: bold;
  margin-left: 900px;
}

.footerMid{
  padding-left:1050px;
  margin: 100px 0;
}
.footerMid>div{
  display: flex;
  height: 100px;
  margin-bottom: 30px;
  line-height: 100px;
  font-size: 36px;
}
.footerMid>div>img{
