}

.alert{
  position: absolute;
  top: 40%;
  left: 35%;
  width: 600px;
  height: 200px;
  font-size: 48px;
  text-align: center;
  line-height: 200px;
  border-radius: 30px;
  background-color: white;
  transition: 0.1s linear;
  opacity: 0;
  color: black;
  z-index: -3;
}
.alertBtn{
  position: absolute;
  top:-35%;
  left:93%;
  z-index: -2;
}
.alertBg{
  position: absolute;
  top: 0;
  left: 0;
  width: 1920px;
  height: 100vh;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.795);
  z-index: -4;
}
.alertOn{
  opacity: 1;
  z-index: 19;
}